.newcollections {
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 10px;
  margin: 0 auto;
}
.newcollections h1 {
  color: #171717;
  font-size: 50px;
  font-weight: 600;
}
.newcollections hr {
  width: 200px;
  height: 6px;
  border-radius: 10px;
  background: #252525;
}
.collections {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  margin: 50px;
  gap: 30px;
}

@media (max-width: 1280px) {

  .newcollections h1 {
    font-size: 40px;
  }
  .newcollections hr {
    width: 160px;
    height: 4px;
  }
  .newcollections-item {
    gap: 20px;
    margin-top: 30px;
  }
}



@media (max-width: 1024px) {

  .newcollections h1 {
    font-size: 30px;
  }
  .newcollections hr {
    width: 120px;
    height: 3px;
  }
  .collections{
    gap: 15px;
    margin-top: 20px;
  }
}

@media (max-width: 800px) {
  .newcollections h1 {
    font-size: 20px;
  }
  .newcollections hr {
    width: 100px;
  }
  .collections {
    gap: 5px;
  }
}

@media (max-width: 500px) {
    .collections{
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 20px;
        margin: 2rem auto;
    }
  }
  