.hero {
  height: 100vh;
  display: flex;
}
.hero-left {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 20px;
  padding-left: 180px;
  line-height: 1.1;
}
.hero-left h2 {
  color: #090909;
  font-size: 23px;
  font-weight: 600;
}
.hero-left p {
  color: #171717;
  font-size: 60px;
  font-weight: 700;
}
.hero-hand-icon {
  display: flex;
  align-items: center;
  gap: 20px;
}
.hero-hand-icon img {
  width: 95px;
}

.hero-latest-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  width: 270px;
  height: 60px;
  border-radius: 75px;
  margin-top: 30px;
  color: rgb(0, 0, 0);
  font-size: 18px;
  font-weight: 500;
  border: .1rem solid #000;
}
.hero-right {
  flex: 1;
  display: flex;
  align-items: center;
}

@media (max-width: 1280px) {
  .hero {
    height: 80vh;
  }
  .hero-left {
    padding-left: 100px;
  }

  .hero-left h2 {
    font-size: 22px;
  }
  .hero-left p {
    font-size: 60px;
  }
  .hero-hand-icon img {
    width: 80px;
  }
  .hero-latest-btn {
    gap: 10px;
    width: 250px;
    height: 60px;
    margin-top: 20px;
    font-size: 18px;
  }
  .hero-right img {
    width: 500px;
  }
}

@media (max-width: 1024px) {
  .hero-left {
    padding-left: 80px;
  }

  .hero-left h2 {
    font-size: 20px;
  }
  .hero-left p {
    font-size: 50px;
  }
  .hero-hand-icon img {
    width: 65px;
  }
  .hero-latest-btn {
    width: 220px;
    height: 50px;
    font-size: 16px;
  }
  .hero-right img {
    width: 400px;
  }
}

@media (max-width: 800px) {
  .hero-left {
    padding-left: 30px;
  }

  .hero-left h2 {
    font-size: 16px;
  }
  .hero-left p {
    font-size: 40px;
  }
  .hero-hand-icon img {
    width: 50px;
  }
  .hero-latest-btn {
    width: 175px;
    height: 40px;

    font-size: 13px;
  }
  .hero-right img {
    width: 300px;
  }
}

@media (max-width: 500px) {
  .hero {
    flex-direction: column;
    height: 70vh;
  }
  .hero-right {
    display: none;
  }
  .hero-left h2 {
    font-size: 18px;
  }
  .hero-left p {
    font-size: 50px;
  }
  .hero-hand-icon img {
    width: 55px;
  }
  .hero-latest-btn {
    width: 200px;
    height: 45px;
    font-size: 14px;
  }
}
